@import url("https://cdn.jsdelivr.net/npm/bootstrap@5.2.3/dist/css/bootstrap.min.css");

.neon-box {
  border-radius: 10px;
  box-shadow: rgba(12, 139, 236, 0.3) 0px 1px 2px 0px,
    rgba(0, 145, 255, 0.15) 0px 1px 3px 1px;
}

@keyframes glowing-animation {
  0% {
    box-shadow: 0 0 5px #0ff;
  }
  50% {
    box-shadow: 0 0 5px #0ff, 0 0 20px #0ff, 0 0 30px #0ff;
  }
  100% {
    box-shadow: 0 0 5px #0ff, 0 0 20px #0ff, 0 0 30px #0ff, 0 0 50px #0ff,
      0 0 60px #0ff;
  }
}

.navbarKinda {
  -ms-overflow-style: none; /* Hide scrollbar on Internet Explorer and Edge */
  scrollbar-width: none; /* Hide scrollbar on Firefox */
}

.navbarKinda::-webkit-scrollbar {
  display: none; /* Hide scrollbar on Chrome, Safari, and Opera */
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 4px;
}
